.container-fluid {
  padding: 1px;
  margin: 0;
  background-color: #1e1e2e;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
}
.headerimg {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: #3499d4;
  align-items: center;
}
.headerimg1 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: #3499d4;
  align-items: center;
}

.headerbtn {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  color: #3499d4;
  align-items: center;
}
.headerbtn button {
  text-transform: uppercase;
  text-decoration: none;
  padding: 10px 37px;
  display: inline-block;
  border-radius: 30rem;
  transition: all 0.2s;
  position: relative;
  font-size: 14px;
  background-color: #f7f7f7;
  color: #1e1e2e;
  transition: 0.2s ease;
}

.headerbtn button:hover {
  background-color: #dfe5f2;
}

.connect {
  background-color: #3499d4;
  color: black;
  border-radius: 5px;
  font-size: 20px;
  border: 1px solid #3499d4;
  align-items: center;
}
.connect:hover {
  background-color: #0483cc;
}
.headerbar {
  padding-top: 120px;
}
.headerbar2 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.row1 {
  display: flex;
}
#headerbar {
  align-items: center;
}
#h3 {
  color: #1d8cf8;
  text-align: start;
  margin-bottom: 20px;
}
#span1 {
  color: #ffffff;
  text-align: start;
}
#span {
  color: #3499d4;
  text-align: start;
}
.box {
  background-color: #3499d4;
  border-radius: 15px;
  height: auto;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.mycustom input[type="text"] {
  border: none;
  width: 100%;
  padding-right: 123px;
}
.mycustom .input-group-prepend {
  position: absolute;
  right: 4px;
  top: 4px;
  bottom: 4px;
  z-index: 9;
}
#floatingInput {
}
.floatingInputrght {
  display: flex;
  justify-content: flex-end;
  justify-self: flex-end;
  /* background-color: blue; */
}

.nputdv {
  display: flex;
  flex-direction: row;
}
